@import "vars";
.secSchool03 {
    @include mq(md) {
        padding: 0 15px;
    }

    >.container {
        >.txt {
            width: 770px;
            margin: auto;

            @include mq(md) {
                width: initial;
            }

            >.title {
                color: #232448;
                font-size: 36px;
                font-weight: 500;
                line-height: 51px;
                text-align: center;
                margin: auto;
                margin-bottom: 27px;

                @include mq(md) {
                    font-size: 30px;
                    line-height: 50px;
                    margin: auto;
                    margin-bottom: 21px;
                }
            }

            >.subtitle {
                font-size: 16px;
                line-height: 30px;
                margin-bottom: 60px;

                @include mq(md) {
                    margin-bottom: 30px;
                }
            }
        }

        >.list {
            display: flex;
            gap: 30px;
            justify-content: center;
            flex-wrap: wrap;

            >.item {
                position: relative;

                @include mq(md) {
                    width: 100%;
                    overflow: hidden;
                    height: 246px;
                }

                >.img {
                    margin: 0;
                }

                >.title {
                    position: absolute;
                    background: rgba(35, 36, 72, 0.9);
                    color: white;
                    width: 100%;
                    bottom: 0;
                    height: 52px;
                    font-size: 20px;
                    text-align: center;
                    line-height: 52px;
                }
            }
        }
    }

    &aa {
        padding-top: 60px;
        padding-bottom: 119px;

        @include mq(md) {
            padding-top: 30px;
            padding-bottom: 80px;
        }
    }

    &ab {
        padding-top: 60px;
        padding-bottom: 100px;

        @include mq(md) {
            padding-top: 30px;
            padding-bottom: 80px;
        }
    }

    &ac {
        padding-top: 60px;
        padding-bottom: 120px;

        @include mq(md) {
            padding-top: 30px;
            padding-bottom: 80px;
        }
    }

    &ad {
        padding-top: 60px;
        padding-bottom: 120px;

        @include mq(md) {
            padding-top: 30px;
            padding-bottom: 80px;
        }
    }

    &ae {
        padding-bottom: 100px;
    }

    &curriculum {
        padding-top: 130px;
        padding-bottom: 140px;
        @include mq(md) {
            padding-top: 100px;
            padding-bottom: 80px;
        }
    }

    &developer {
        padding-bottom: 140px;
        @include mq(md) {
            padding-bottom: 80px;
        }
    }
}